import { Component, OnInit } from '@angular/core';
import { UserService } from '../../../services/user-service/user.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-summary-popup',
  templateUrl: './summary-popup.component.html',
  styleUrls: ['./summary-popup.component.scss']
})

export class SummaryPopupComponent implements OnInit {
  // this var from services, itis mean kilocalories, proteins, carbohydrates, fats
  kpcf: {};
  userName: string;

  constructor(private userService: UserService, private router: Router) {
  }

  ngOnInit() {
    this.userService.loggedUser.subscribe(
      (dataUser) => {
        this.userName = dataUser.name;
        this.kpcf = dataUser.kpcf;
      }
    );

    /* this.userName = this.userService.loggedUser.name;
    this.kpcf = this.userService.loggedUser.kpcf; */
  }

  close() {
    this.router.navigate([{outlets: {popup: null}}])
   .then(() => this.router.navigate(['/main']));

  }

}



