import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';

import { UserService } from '../../../services/user-service/user.service';
import { ProductService } from '../../../services/product-service/product.service';
import { Product } from '../../../models/product';
import { MealService } from '../../../services/meal-service/meal.service';
import { Meal } from '../../../models/meal';
import { DailyProgressService } from '../../../services/daily-progress-service/daily-progress.service';
import { DailyProgress } from '../../../models/daily_progress';
import { Dish } from '../../../models/dish';
import { DishService } from '../../../services/dish-service/dish.service';


@Component({
  selector: 'app-search-weight',
  templateUrl: './search-weight.component.html',
  styleUrls: ['./search-weight.component.scss']
})

export class SearchComponent implements OnInit {
  stateMW: string;
  stateMWSearch: string;
  stateMWWeight: string;
  searchProd = '';

  formWeight: FormGroup;
  mainCalories: number;
  mainProteins: number;
  mainFats: number;
  mainCarbohydrates: number;

  adminID = 'admin';
  userID: string;

  productsCommon = [];

  meals: Meal[];
  meal = new Meal;

  todayDate: Date;
  currentDate: string;

  constructor(public productService: ProductService,
              private mealService: MealService,
              private progressService: DailyProgressService,
              private userService: UserService,
              private dishService: DishService) { }

  ngOnInit() {
    this.stateMW = 'mwClose';
    this.stateMWSearch = 'mwSearchClose';
    this.stateMWWeight = 'mwWeightClose';

    this.formWeight = new FormGroup({
      name: new FormControl({value: this.meal.name, disabled: true}),
      weight: new FormControl({value: null},
        Validators.required,
        Validators.pattern['^[0-9]+$']
      ),
      calories: new FormControl({value: this.meal.calories, disabled: true}),
      proteins: new FormControl({value: this.meal.proteins, disabled: true}),
      fats: new FormControl({value: this.meal.fats, disabled: true}),
      carbohydrates: new FormControl({value: this.meal.carbohydrates, disabled: true})
    });

    this.todayDate = new Date();
    this.currentDate = this.todayDate.toDateString();

    this.userService.loggedUser.subscribe( currentUser => {
      this.userID = currentUser.id;
      this.dishService.getDishes(currentUser.id).valueChanges().subscribe(dishes => {
        this.productsCommon = dishes;
      });

      this.productService.getProductsConcat(currentUser.id, this.adminID).subscribe(prod => {
        prod.map(item => this.productsCommon.push(item));
        this.productsCommon.sort((a, b) => {
          if (a.name < b.name) {
            return -1;
          }
          if (a.name > b.name) {
            return 1;
          }
          return 0;
        });
      });

      this.mealService.getMeals(currentUser.id, this.currentDate).valueChanges().subscribe(meals => {
        meals.sort((a, b) => {
          if (a.dateObject < b.dateObject) {
            return -1;
          }
          if (a.dateObject > b.dateObject) {
            return 1;
          }
          return 0;
        });
        this.meals = meals;
      });
    });
  }

  open() {
    this.stateMWSearch = 'mwSearchOpen';
    this.stateMW = 'mwOpen';
  }

  chooseProd(product) {
    this.meal.name = product.name;
    this.meal.calories = this.mainCalories = product.calories;
    this.meal.proteins = this.mainProteins = product.proteins;
    this.meal.fats = this.mainFats = product.fats;
    this.meal.carbohydrates = this.mainCarbohydrates = product.carbohydrates;

    this.stateMWSearch = 'mwSearchClose';
    this.stateMWWeight = 'mwWeightOpen';
  }

  close() {
    this.stateMW = 'mwClose';
    this.searchProd = '';
    this.stateMWSearch = 'mwSearchClose';
    this.stateMWWeight = 'mwWeightClose';
  }

  back() {
    this.stateMWWeight = 'mwWeightClose';
    this.stateMWSearch = 'mwSearchOpen';
  }

  addMeal(meal: Meal) {
    this.mealService.addMealToDB(meal);
    this.progressService.up(this.getProgress(meal));
    this.meal.name = '';
    this.meal.weight = null;
    this.meal.calories = null;
    this.meal.proteins = null;
    this.meal.fats = null;
    this.meal.carbohydrates = null;

    this.stateMW = 'mwClose';
    this.stateMWWeight = 'mwWeightClose';
  }

  countKPCF() {
    if (this.meal.weight !== 100) {
      this.meal.calories = Math.ceil((this.mainCalories * this.meal.weight) / 100);
      this.meal.proteins = Math.ceil((this.mainProteins * this.meal.weight) / 100);
      this.meal.fats = Math.ceil((this.mainFats * this.meal.weight) / 100);
      this.meal.carbohydrates = Math.ceil((this.mainCarbohydrates * this.meal.weight) / 100);
    } else {
      this.meal.calories = this.mainCalories;
      this.meal.proteins = this.mainProteins;
      this.meal.fats = this.mainFats;
      this.meal.carbohydrates = this.mainCarbohydrates;
    }
  }

  getProgress(meal: Meal): DailyProgress {
    const progress = new DailyProgress();
    progress.user_id = this.userID;
    progress.calories = meal.calories;
    progress.proteins = meal.proteins;
    progress.fats = meal.fats;
    progress.carbohydrates = meal.carbohydrates;
    progress.date = Date.now();

    return progress;
  }


}



