import {Component, OnDestroy, OnInit} from '@angular/core';
import { showMessage, showHare } from './hare.animation';
import { Router } from '@angular/router';

@Component({
  selector: 'app-hare',
  templateUrl: './hare.component.html',
  styleUrls: ['./hare.component.scss'],
  animations: [ showMessage, showHare ]
})

export class HareComponent implements OnInit, OnDestroy {

  sub: any;
  location: any;
  messageClass: string;
  animateStyleState: string;
  someHare: any;
  someMessage: any;
  readonly helloHare = '../assets/img/helloHare.png';
  readonly helloMessage = '../assets/img/helloMessage.png';
  readonly singUpHare = '../assets/img/singUpHare.png';
  readonly singUpMessage = '../assets/img/singUpMessage.png';
  readonly logInHare = '../assets/img/logInHare.png';
  readonly logInMessage = '../assets/img/logInMessage.png';
  readonly notfoundHare = '../assets/img/notfoundHare.png';
  readonly notfoundMessage = '../assets/img/notfoundMessage.png';

  constructor(private router: Router) { }

  ngOnInit() {
    this.sub = this.router.events.subscribe((data: any) => {
      this.location = data.url;
      this.animateStyleState = 'default';

      if (this.location === '/login') {
          this.messageClass = 'helloMessage';
          this.someMessage = this.helloMessage;
          this.someHare = this.helloHare;
          this.getAnimation();
      } else if (this.location === '/') {
          this.messageClass = 'helloMessage';
          this.someMessage = this.helloMessage;
          this.someHare = this.helloHare;
          this.getAnimation();
      } else if (this.location === '/signup') {
          this.messageClass = 'singUpMessage';
          this.someMessage = this.singUpMessage;
          this.someHare = this.singUpHare;
          this.getAnimation();
      } else if (this.location === '/main') {
          this.messageClass = 'logInMessage';
          this.someMessage = this.logInMessage;
          this.someHare = this.logInHare;
          this.getAnimation();
      } else {
          this.messageClass = 'notfoundMessage';
          this.someMessage = this.notfoundMessage;
          this.someHare = this.notfoundHare;
          this.getAnimation();
      }
    });
  }

  ngOnDestroy() {
    this.sub.unsubscribe();
  }

  private getAnimation() {
    setTimeout(() => {
      if (this.animateStyleState === 'default') {
        this.animateStyleState = 'show';
      }
    }, 10);
  }
}



